<template>
  <div class="card bg-dark col-12 mx-auto p-3" style="border-radius: 0">
    <div class="row">
      <h3>{{ title }}</h3>
      <div class="col-6">
        <div class="input-group">
          <span class="input-group-text">Operaci&oacute;n</span>
          <input
            type="text"
            class="form-control form-control-sm"
            v-model="b5"
          />
        </div>
      </div>
      <div class="col-12 mt-3">
        <table class="table table-sm table-light table-striped table-bordered">
          <thead>
            <tr class="text-center">
              <th>Casa</th>
              <th>Tasa</th>
              <th>Dolar</th>
              <th>Soles</th>
              <th>Euro</th>
            </tr>
          </thead>
          <tbody class="text-center">
            <tr>
              <td>#1</td>
              <td>
                <input
                  type="text"
                  class="form-control form-control-sm mx-auto text-center"
                  v-model="b3"
                />
              </td>
              <td>{{ c3().toFixed(2) }} &dollar;</td>
              <td>{{ (c3() * currencies.USDPEN.value).toFixed(0) }}</td>
              <td>{{ (c3() * currencies.USDEUR.value).toFixed(1) }}</td>
            </tr>
            <tr>
              <td>#2</td>
              <td>
                <input
                  type="text"
                  class="form-control form-control-sm mx-auto text-center"
                  v-model="b4"
                />
              </td>
              <td>{{ c4().toFixed(2) }} &dollar;</td>
              <td>{{ (c4() * currencies.USDPEN.value).toFixed(0) }}</td>
              <td>{{ (c4() * currencies.USDEUR.value).toFixed(1) }}</td>
            </tr>
            <tr>
              <td colspan="3" class="text-end"><b>Gana</b></td>
              <td>{{ d5().toFixed(1) }}&dollar;</td>
              <td>{{ ((d5() * b5) / 100).toFixed(2) }}%</td>
            </tr>
            <tr>
              <td class="bg-dark" colspan="5"></td>
            </tr>
            <tr>
              <td colspan="2"></td>
              <td>
                <div class="input-group">
                  <input
                    type="text"
                    class="text-center form-control form-control-sm"
                    v-model="c8"
                  />
                </div>
              </td>
              <td>
                <div class="input-group">
                  <input
                    type="text"
                    class="text-center form-control form-control-sm"
                    v-model="d8"
                  />
                </div>
              </td>
              <td>
                <div class="input-group">
                  <input
                    type="text"
                    class="text-center form-control form-control-sm"
                    v-model="e8"
                  />
                </div>
              </td>
            </tr>
            <tr>
              <td>#1</td>
              <td>{{ b3 }}</td>
              <td>{{ c9().toFixed(2) }}</td>
              <td>{{ d9().toFixed(2) }}</td>
              <td>{{ e9().toFixed(2) }}</td>
            </tr>
            <tr>
              <td>#2</td>
              <td>{{ b4 }}</td>
              <td>{{ c10().toFixed(2) }}</td>
              <td>{{ d10().toFixed(2) }}</td>
              <td>{{ e10().toFixed(2) }}</td>
            </tr>
            <tr>
              <td><b>Operacion</b></td>
              <td>{{ b11().toFixed(2) }} &dollar;</td>
              <td><b>Gana</b></td>
              <td>{{ d11().toFixed(2) }}</td>
              <td>{{ (100 * (d11() / b11())).toFixed(2) }}%</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "calculator",
  props: ["currencies", "title"],
  data() {
    return {
      b5: 100,
      c8: 100,
      d8: 0,
      e8: 0,
      b3: "2.5",
      b4: "3",
    };
  },
  methods: {
    b11() {
      return this.c9() + this.c10();
    },
    c3() {
      return (
        this.b5 *
        (parseFloat(this.b4) / (parseFloat(this.b3) + parseFloat(this.b4)))
      );
    },
    c4() {
      return (
        this.b5 *
        (parseFloat(this.b3) / (parseFloat(this.b3) + parseFloat(this.b4)))
      );
    },
    c9() {
      if (this.d8 != "0" && this.d8 != "") {
        return this.d9() * this.currencies.PENUSD.value;
      } else {
        if (this.c8 != "0" && this.c8 != "") {
          return (
            parseFloat(this.c8) *
            (parseFloat(this.b4) /
              (parseFloat(this.b3) + parseFloat(this.b4)) /
              (1 -
                parseFloat(this.b4) /
                  (parseFloat(this.b3) + parseFloat(this.b4))))
          );
        } else {
          return this.e9() / this.currencies.USDEUR.value;
        }
      }
    },
    c10() {
      if (this.d8 != "0" && this.d8 != "") {
        return this.d10() * this.currencies.PENUSD.value;
      } else {
        if (this.c8 != "0" && this.c8 != "") {
          return parseFloat(this.c8);
        } else {
          return this.e10() / this.currencies.USDEUR.value;
        }
      }
    },
    d5() {
      return parseFloat(this.b3) * this.c3() - this.b5;
    },
    d9() {
      if (this.d8 != "0" && this.d8 != "") {
        return (
          parseFloat(this.d8) *
          (parseFloat(this.b4) /
            (parseFloat(this.b3) + parseFloat(this.b4)) /
            (1 -
              parseFloat(this.b4) /
                (parseFloat(this.b3) + parseFloat(this.b4))))
        );
      } else {
        return this.e9() / this.currencies.PENEUR.value;
      }
    },
    d10() {
      if (this.d8 != "0" && this.d8 != "") {
        return parseFloat(this.d8);
      } else {
        return this.e10() / this.currencies.PENEUR.value;
      }
    },
    d11() {
      return parseFloat(this.b3) * this.c9() - this.b11();
    },
    e9() {
      if (this.d8 != "0" && this.d8 != "") {
        return this.c9() * this.currencies.USDEUR.value;
      } else {
        if (this.c8 == "0") {
          return (
            parseFloat(this.e8) *
            (parseFloat(this.b4) /
              (parseFloat(this.b3) + parseFloat(this.b4)) /
              (1 -
                parseFloat(this.b4) /
                  (parseFloat(this.b3) + parseFloat(this.b4))))
          );
        } else {
          return this.c9() * this.currencies.USDEUR.value;
        }
      }
    },
    e10() {
      if (this.d8 != "0" && this.d8 != "") {
        return this.c10() * this.currencies.USDEUR.value;
      } else {
        if (this.c8 != "0" && this.c8 != "") {
          return this.c10() * this.currencies.USDEUR.value;
        } else {
          return parseFloat(this.e8);
        }
      }
    },
  },
  components: {},
};
</script>

